.job{border-radius:10px;box-sizing:border-box;padding:10px;border:1px solid #cfd2d7;margin:10px;transition:border-color .2s ease;position:relative;background-color:#fff;cursor:pointer}.job:hover,.job.selected{border-color:#1e47a9}.job .job-header{height:30px;display:flex;flex-direction:row;justify-content:space-between}.job .job-header .job-logo{max-width:200px;max-height:30px}.job .job-header .job-actions-container{position:absolute;right:10px;display:flex;flex-direction:row}.job .job-header .job-actions-container .job-action-button{opacity:.1;cursor:pointer;height:25px}.job .job-header .job-actions-container .job-action-button:hover{opacity:.7}.job .spaced{margin:10px 0}