.job-requests-component{max-height:200px;margin-left:10px;width:calc(100% - 10px);box-sizing:border-box;display:flex;flex-direction:column}.job-requests-component .job-requests-container{padding:10px;box-sizing:border-box;height:100%;overflow:auto;background-color:#f9f9f9;border-radius:10px 10px 0 0;border-bottom:1px solid #97989b}.job-requests-component .job-requests-container .job-request{border:1px solid #cfd2d7;border-radius:10px;display:flex;flex-direction:row;padding:10px;box-sizing:border-box;justify-content:space-between;margin-bottom:5px;background-color:#fff}.job-requests-component .job-requests-add-container{height:30px;display:flex;flex-direction:row;border-top:none;border-radius:0 0 10px 10px;overflow:hidden}.job-requests-component .job-requests-add-container input{width:100%;border:none;outline:none;padding-left:10px}.job-requests-component .job-requests-add-container .job-requests-add-container-add-button{width:30px;border-left:1px solid #d8dbe3;display:flex;justify-content:center;align-items:center;background-color:#fff;cursor:pointer}.job-requests-component .job-requests-add-container .job-requests-add-container-add-button.disabled{opacity:.3;pointer-events:none}.job-requests-component .job-requests-add-container .job-requests-add-container-add-button:hover{background-color:#f9f9f9}.job-requests-component .job-requests-add-container .job-requests-add-container-add-button img{width:17px}