.paginator{display:flex;flex-direction:row;width:100%;height:50px;box-shadow:0px -4px 6px 0px #a9a9a9;justify-content:center;align-items:center;z-index:2;background-color:#fff}.paginator button{height:30px;border-radius:5px;cursor:pointer;background-color:#eaecf1;border:1px solid #eaecf1;transition:border-color .2s ease}.paginator button:disabled{cursor:default}.paginator button:not([disabled]):hover{border-color:#1e47a9}.paginator .counter{height:30px;line-height:30px;font-size:14px;margin:0 10px}