.filter-component{border:1px solid #cfd2d7;background-color:#fff;border-radius:10px;height:48%;overflow:hidden}.filter-component .filter-component-title{height:30px;line-height:30px;padding-left:5px}.filter-component .filter-component-filter-cloud{height:calc(100% - 60px);border-top:1px solid #97989b;border-bottom:1px solid #97989b;box-sizing:border-box;background-color:#fff;z-index:1;display:flex;flex-direction:row;padding:5px;flex-wrap:wrap;width:100%}.filter-component .filter-component-filter-cloud .filter-component-filter-cloud-filter-item{box-shadow:0 0 5px 0 #989ba2;background-color:#eaecf1;border:1px solid #eaecf1;height:25px;line-height:15px;padding:5px;box-sizing:border-box;border-radius:5px;margin:5px}.filter-component .filter-component-add-container{height:30px;display:flex;flex-direction:row;border-top:none;border-radius:0 0 10px 10px;overflow:hidden;box-shadow:0 -4px 6px 0 #a9a9a9;z-index:2}.filter-component .filter-component-add-container input{width:100%;border:none;outline:none;padding-left:10px}.filter-component .filter-component-add-container .filter-component-add-container-add-button{width:30px;border-left:1px solid #d8dbe3;display:flex;justify-content:center;align-items:center;background-color:#fff;cursor:pointer}.filter-component .filter-component-add-container .filter-component-add-container-add-button.disabled{opacity:.3;pointer-events:none}.filter-component .filter-component-add-container .filter-component-add-container-add-button:hover{background-color:#f9f9f9}.filter-component .filter-component-add-container .filter-component-add-container-add-button img{width:17px}